@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Azeret+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Azeret+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  max-width: 100%;
}
#header {
    background: linear-gradient(228deg, rgba(0,171,255,1) 10%, rgba(1,45,108,1) 87%); 
    padding: 0.5em 1.5em 0 0.5em;
    display: flex;
    flex-direction: column;
    justify-content: left;
}

#name-container, #occupation-container {
    display: block;
}

#occupation-and-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#occupation-container{
    padding-bottom: 1em;
}

#nav-links-container {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin: 0;
    
}

.nav-link {
    font-weight: bold;
    font-size: 20px;
    text-decoration: none;
    color: rgb(6, 6, 75);
    border-radius: 5px;
    padding: 13px;
    background-color: transparent;
    transition: color 0.6s ease, background-color 0.6s ease;
}

.nav-link:hover, .nav-link:focus {
    color: rgb(231, 231, 96);
    cursor: pointer;
    background-color: rgb(1, 45, 108);
    border-radius: 5px;
}

#name, #occupation {
    color: rgb(231, 231, 96);
    display: inline;
}

#name {
    font-family: 'Mukta', sans-serif;
    font-size: 3em;
    margin: 0.5em 1em 0.5em 1em;
}

#occupation {
    font-family: 'Azeret Mono', monospace;
    margin: 1em 0.5em 0 2em;
}

#name-header-icon {
    color: white;
    font-size: 2em;
}

.occupation-icons{
    color: white;
    font-size: 1.5em;
    margin: 0 0.3em;
}

@media only screen and (max-width: 880px){

    .occupation-icons, #name-header-icon {
        display: none;
    }

    #occupation-and-nav{
        flex-direction: column;
        justify-content: center;
    }

    #occupation {
        display: inline-block;
        margin: 0 auto 0 1em;
        text-align: center;
    }

    #nav-links-container {
        margin-bottom: 1em;  
    }

}

@media only screen and (max-width: 680px){

    #header {
        background: linear-gradient(33deg, rgba(9,9,121,1) 34%, rgba(0,212,255,1) 98%);
        text-align: center;
    }

    #name {
        margin-left: 0.5em;
    }

    #nav-links-container {
        justify-content: center;
        padding-left: 0;
    }
    .nav-link {
        /*fixes contrast on dark navbar*/
        color: white;
    }
}

@media only screen and (max-width: 400px){

    #header {
        background: rgba(9,9,121,1);
    }

    .nav-link{
        font-size: 18px;
    }
}

#intro-header {
    text-align: left;
    margin-left: 7.5%;
}

#intro-content{
    margin: 2em 7.5% 2em 7.5%;
    padding: 0;
    display: grid;
    grid-template-columns: 2fr 1fr;
    place-items: center;
    grid-column-gap: 10px;
    grid-row-gap: 5px;

}

.intro-text {
    margin: 0 auto;
}

#p1{
    grid-column: 1/2;
    place-self: end;
}

#p2 {
    grid-column: 1/2;
}

#p3 {
    grid-column: 1/2;
}


.profile-img {
    height: 15em;
    width: 15em;
    border-radius: 30%;
    grid-column: 2/3;
    grid-row: 1/4;
}

@media only screen and (max-width: 640px){

        .profile-img {
            grid-row: 1/3;
        }

        #p3 {
            grid-column: 1/4;
        }

}

@media only screen and (max-width: 510px){

        #intro-content {
            grid-template-columns: 2fr 1fr;
            grid-template-rows: repeat(4, -webkit-min-content);
            grid-template-rows: repeat(4, min-content);
            grid-row-gap: 20px;
        }

        .profile-img {
            grid-column: 1/3;
            grid-row: 1/2;
        }
        
        #p1 {
            grid-column: 1/3;
            grid-row: 2/3;
        }
        #p2 {
            grid-column: 1/3;
            grid-row: 3/4;
        }
        #p3 {
            grid-column: 1/3;
            grid-row: 4/5;
        }

}


:-moz-focusring {
    outline: 3px solid blue;
    border-radius: 5px;
  }

#my-work {
    text-align: left;
    margin-left: 7.5%;

}

hr {
    width: 85%;
}

#arrows-and-projects-container{
    display: flex;
    flex-direction: row;
    align-items: center;
}

#swipe-icon-container {
    display: none;
}


#swipe-icon {
    display: none;
}

#projects-container{
    display: flex;
    flex-direction: row;
    padding: 1em 0;
    width: 46em;
    height: 20em;
    margin: 0 3vw;
    overflow-x: scroll;
    -ms-scroll-snap-type: x proximity;
        scroll-snap-type: x proximity;
    -ms-overflow-style: none;  /* hide default scrollbar for IE and Edge */
    scrollbar-width: none;    /*hide it for Firefox*/
    scroll-behavior: smooth; 
}

/*hide default scrollbar for Chrome, Safari & Opera*/
#projects-container::-webkit-scrollbar{
    display: none;
}

#prev-button, #next-button {
    background-color: transparent;
    border: none;
    margin: 0 auto;
}

.arrow {
    font-size: 3.5em;
    color: rgb(6, 6, 75);
    background-color: white;
    border-radius: 50%;
    transition: color 0.3s;
}

.arrow-disabled {
    font-size: 3.5em;
    color: gray;
    background-color: white;
    border-radius: 50%;
    transition: color 0.3s;
}

.arrow:hover, .arrow:focus {
    color: green;
    cursor: pointer;
}

.project {
    scroll-snap-align: start;
    display: flex;
    flex-direction: column;
    box-sizing: content-box;
    height: 16em;
    width: 18em;
    border: solid 2px black;
    border-radius: 5px; 
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    transition: box-shadow 0.3s ease;
}

.project:hover, .project:focus {
    cursor: pointer; 
    box-shadow: 3px 5px 15px 3px #000000;
}


.thumbnail-img {
    width: 100%;
    height: auto;
}


/* captionActive styles (also used for mobile devices):*/
.active-caption {
    text-align:center;
    font-weight: bold;
    font-size: 17px;
    border-radius: 0 0 2px 2px; 
    background-color: rgba(140, 140, 140, 0.9);
    width: inherit;
    height: 30%;
    margin-top: 60%;
    padding-top: 0.5em;
    color: black;  
    transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
    -webkit-transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
    -moz-transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
    -ms-transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
}

/*captionInactive styles:*/

.inactive-caption {
text-align: center;
font-weight: bold;
font-size: 17px;
border-radius: 0 0 2px 2px;
background-color: rgba(140, 140, 140, 0.9);
width: inherit;
height: 0;
margin-top: 89%;
padding-top: 0;
color: transparent; 
transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
-webkit-transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
-moz-transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
-ms-transition: height 0.5s, margin-top 0.5s, padding-top 0.5s, color 0.5s;
}

#caption-links-container{
    height: 70%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.caption-link {
    margin: 0 0.5em;
    text-decoration: none;
    font-size: 14px;
    padding: 0.5em;
    border-radius: 7px;
    color: white;
    background-color: black;
    border: 2px solid black;
    transition: color 0.5s, background-color 0.5s;

}

.inactive-caption-link {
    margin: 0 0.5em;
    text-decoration: none;
    font-size: 14px;
    padding: 0.5em;
    border-radius: 7px;
    color: transparent;
    background-color: transparent;
    border: 2px solid transparent;
}

.caption-link:hover, .caption-link:focus {
    color: black;
    background-color: white;
}

@media only screen and (max-width: 998px){
    
    #projects-container {
        width: 21em;
    }
    #prev-arrow, #next-arrow {
        margin: 0;
    }

    .project {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
        scroll-snap-align: center;
    }

}

@media only screen and (max-width: 640px){


    #swipe-icon-container {
        display: flex;
        justify-content: center;
    }

    #swipe-icon {
        display: inline;
        height: 3em;
        width: auto;
    }
    
    .project {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
        margin-left: 10vw;
        margin-right: 14vw;
    }
    
    #next-button, #prev-button, .arrow, .arrow-disabled {
        
        visibility: hidden;
        width: 0;
        height: 0;
    }

    #arrows-and-projects-container{
        display: block;
        
    }

    #projects-container {
        -ms-overflow-style: auto;  /* show default scrollbar for IE and Edge */
        scrollbar-width: auto;    /*show it for Firefox*/
        width: 80vw;
        margin: 0 auto;
    }
    /*show default scrollbar for Chrome, Safari & Opera*/
    #projects-container::-webkit-scrollbar{
        display: block;
    }

}

@media only screen and (max-width: 440px){

    #projects-container {
        margin: 0 5vw;
        width: 90vw;
    }

    .project {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
        margin-left: 1em;
    }


}

#contact-header, #contact-content {
    text-align: left;
    margin-left: 7.5%;
}

#contact-content {
    display: flex;
    flex-direction: row;
}

#social-icons{
    width: 30%;
    margin-left: 1.5em;
    height: -webkit-min-content;
    height: min-content;
    display: flex;
    flex-direction: column;
}

#in, #gh {
    margin-top: 40px;
}

.contact-icon-container{
    
    box-shadow: 0;
    transition: box-shadow 0.5s;
    padding: 0 5px;
    border: 2px solid black;
    border-radius: 7px;
    background-color: white;
}

.contact-icon-container:hover, .contact-icon-container:active, .contact-icon-container:focus {
    transition: box-shadow 0.5s;
    box-shadow: 3px 3px 7px 3px black;
}

.contact-method {
    font-size: 1rem;
    font-weight: bold;
}

.contact-icon {
    text-decoration: none;
    font-size: 2.5rem;
    margin-top: 5px;
    width: -webkit-min-content;
    width: min-content;
    transition: color 0.5s;

}

#cv-icon{
    color: #551a8b;
}

#gh-icon {
    color: black;
}

#in-icon {
    color: #0077b5;   
}

@media only screen and (max-width: 770px){

        #contact-content{
            display: grid;
            margin: 0 7.5% 3em 7.5%;
            grid-row-gap: 20px;
        }

        #social-icons {
            width: 70%;
            display: inline-grid;
            grid-template-columns: 2fr 1fr;
            place-items: center;
            grid-template-areas:
            'text1 icon1'
            'text2 icon2'
            'text3 icon3';
            grid-row-gap: 10px;
        }

        #gh {
            margin-top: 0;
            grid-area: text2;
        }

        #gh-icon{
            grid-area: icon2;
        }

        #in {
            margin-top: 0;
            grid-area: text3;
        }

        #in-icon{
            grid-area: icon3;
        }

        #cv {
            margin-top: 0;
            grid-area: text1;
        }

        #cv-icon {
            grid-area: icon1;
        }


}

@media only screen and (max-width: 510px){

        #contact-content {
            margin: 0 0 3em 2px;
        }

}


#icons-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.email-icon {
    color: rgb(1, 45, 108);
    font-size: 2.5em;
}

#email-form-container{
    background-color: rgb(226, 232, 241);
    border-radius: 7px;
    border: 2px solid black;
    width: 50%;
    padding: 1em 2em;
    
}

#form {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 7fr;
    grid-template-columns: 1fr 2fr 2fr 2fr;
    grid-gap: 10px;
    place-items: left;
}

#req-legend{
    font-size: 13px;
}

.form-label{
    grid-column: 1/2;
    font-family: 'Mukta', sans-serif;
    font-weight: bold;
}

.form-input{
   grid-column: 2/3;
}

#message-field{
    resize: none;
    overflow: auto;
    grid-column: 2/4;
}

#recaptcha-widget {
    grid-column: 2/4;
}

#submit-button {
    border: 2px solid black;
    border-radius: 5px;
    height: 2em;
    background-color: #00abff;
    color: rgb(6, 6, 75);
    font-weight: bold;
    font-family: 'Mukta', sans-serif;
    font-size: 20px;
    transition: color 0.5s, background-color 0.5s;
    grid-column: 2/3;
}

#submit-button:hover, #submit-button:active, #submit-button:focus {
    background-color: rgb(1, 45, 108);
    color: rgb(231, 231, 96);
    transition: color 0.5s, background-color 0.5s;
    cursor: pointer;
}

#result-container, #error-container {
    text-align: center;
}

@media only screen and (max-width: 920px){

    #recaptcha-widget {
        grid-column: 1/3;
    }

}

@media only screen and (max-width: 770px){

    #email-form-container{
        padding: 1em;
        width: 87%;
        max-width: 100vw;
    }
}

@media only screen and (max-width: 400px){
    #recaptcha-widget {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
        margin-left: -1em;
    }
}




#app-wrapper {
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    padding-bottom: 3em;
}

#content-background {
    width: 85%;
    margin: 3em auto 0 auto;
    padding: 1em;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.6);
}

#page-footer {
    text-align: center;
    font-size: 12px;
    max-width: 95%;
}

.footer-icon {
    margin: 0 4px;
}

@media only screen and (max-width: 770px){
    #content-background {
        padding-left: 0;
        padding-right: 0;
        box-sizing: border-box;
        width: 100%;
        max-width: 100%;
        margin: 2em 0;
    }
}
